import BasePerimeter from '@/accessControl/BasePerimeter';
import GenericPerimeter from '@/accessControl/GenericPerimeter';

const LG = console.log; // eslint-disable-line no-console, no-unused-vars

const RESOURCE_NAME = 'Example';
class TestComponentPerimeter extends GenericPerimeter {
  constructor(purpose, opts = {}) {
    super(purpose, opts);
    this.resource = RESOURCE_NAME;
  }
}


export default {
  protectedPerimeter: new TestComponentPerimeter({
    purpose: 'elementLevelProtection',
  }),
  classifiedPerimeter: new BasePerimeter({
    purpose: 'routeLevelProtection',
    resource: RESOURCE_NAME,
    govern: {
      'can route': function canRoute(aModule) {
        LG(`
    mayView      ${this.mayView()}
    mayComment   ${this.mayComment()}
    mayAlter     ${this.mayAlter()}
    mayAuthorize ${this.mayAuthorize()}
`);
        LG(aModule);
        return this.mayView();
      },
    },
  }),
};



