import { perimeters as AdminPerimeters, accessGroups as AdminAccessGroups } from './Admin';
import { perimeters as SalesPerimeters, accessGroups as SalesAccessGroups } from './Sales';
import { perimeters as TestPerimeters, accessGroups as TestAccessGroups } from './Tests';

export const perimeters = Object.assign( // eslint-disable-line import/prefer-default-export
  {},
  AdminPerimeters,
  SalesPerimeters,
  TestPerimeters,
);

export const accessGroups = Object.assign( // eslint-disable-line import/prefer-default-export
  {},
  AdminAccessGroups,
  SalesAccessGroups,
  TestAccessGroups,
);



