import { Ability, AbilityBuilder } from '@casl/ability';

import { createSandbox, StrictGoverness } from 'vue-kindergarten';

import { store } from '@/store';
// import { routes } from '@/components/MainLayout';

import perimeterDefs from './perimeters';
import accessGroups from './accessGroups';
// import { Levels } from './BasePerimeter';

import Levels from './Levels';


const LG = console.log; // eslint-disable-line no-console, no-unused-vars

Levels.idxAliases.forEach((lvl) => {
  if (lvl !== 'none') Ability.addAlias(lvl, Levels.aliases[lvl]);
});

export const abilities = AbilityBuilder.define((can) => {
  Object.entries(accessGroups).forEach(([key, value]) => {
    LG(`+++++++++++++++++ accessGroups ++++++++++++++++ ${key}`);
    LG(value);
    can('do_nothing', value);
  });
});

// Vue.use(abilitiesPlugin, abilities);
// Vue.component('Can', Can);


export const currentUser = {
  // Getter of your current user.
  // If you use store, then store will be passed
  child: vx => (vx ? vx.state.a12n.user : null),

  // child: (store) =>
  //   return store.state.user;
  //   // or
  //   // return decode(localStorage.getItem('jwt'));
  //   // or your very own logic..
  // }
};

const routeAccessControls = (t, f, nxt) => { // eslint-disable-line no-unused-vars
  if (!(window.ability && window.ability.can)) return null;

  LG(`




    accessControl/index ===>
    routeAccessControls beforeEach ==> Query has '${t.name}'.`);
  if (t.name === 'home') return null;
  if (window.ability.can('only_view', t.name)) return null;
  return false;

  // LG(window.ability);
  // LG(`window.ability.can('view', '${t.name}')`);
  // LG(window.ability.can('view', t.name));

  // LG(`window.ability.can('only_view', '${t.name}')`);
  // LG(window.ability.can('only_view', t.name));

  // LG(`window.ability.can('comment', '${t.name}')`);
  // LG(window.ability.can('comment', t.name));


  // LG(window.ability.cannot('only_view', t.name));
  // if (window.ability.cannot('only_view', t.name)) return false;
  // return null;
};

const OLDprepareSandBoxGuards = (t, f, nxt) => { // eslint-disable-line no-unused-vars
  LG(`




    accessControl/index ===>
    prepareSandBoxGuards beforeEach ==> Query has '${t.name}'.`);
  const comp = String(t.name).split('');
  const ret = [];
  while (comp.length > 0) {
    let ch = comp.shift();
    if (ch === '/') ch = comp.shift().toUpperCase();
    ret.push(ch);
  }
  const moduleName = ret.join('');
  // LG(`accessControl beforeEach ==> comp =  '${comp.length}'.`);
  // LG(`accessControl beforeEach ==> comp =  '${comp.length}'.`);
  LG(`accessControl beforeEach ==> ret =  '${moduleName}'.`);
  // for (const char of moduleName) {
  //   LG(`accessControl beforeEach ==>  '${char}'.`);
  //   comp = char;
  // }
  // LG(`accessControl beforeEach ==> comp =  '${comp}'.`);

  if (window.lgr) window.lgr.info(`accessControl beforeEach ==> Query has '${moduleName}'.`);
  const perimeter = perimeterDefs[`${moduleName}Perimeter`];
  if (perimeter) {
    let sandbox = null;

    LG(`
      accessControl beforeEach ==> currentUser`);
    LG(currentUser);
    LG(store);
    sandbox = createSandbox(currentUser.child(store), {
      perimeters: [
        perimeter,
      ],
      // governess: new RouteGoverness({ from, t, nxt }),
      governess: new StrictGoverness(),
    });
    LG('~~~~~~~~~~~~   sandbox   ~~~~~~~~~~~~');
    LG(sandbox);
    LG(sandbox.guard('route'));
    return sandbox.guard('route');
  }
  return nxt();
};

export const beforeEach = [
  routeAccessControls,
];

const collectDomains = () => {
  const ret = {};
  Object.entries(perimeterDefs).map((p) => {
    const name = p[1].resource;
    ret[name] = {
      name,
      level: Levels.alvls[Levels.NO_ACCESS],
    };
    return true;
  });
  return ret;
};

const state = {
  sandbox: {},
  user: {
    // info: {
    //   name: 'Bob',
    //   role: 'admin',
    // },
    permissions: {
      // Example: Levels.NO_ACCESS,
      Example: Levels.VIEW_ONLY,
      // Example: Levels.COMMENT,
      // Example: Levels.ALTER,
      // Example: Levels.OWN,

      // Invoice: Levels.NO_ACCESS,
      Invoice: Levels.VIEW_ONLY,
      // Invoice: Levels.COMMENT,
      // Invoice: Levels.ALTER,
      // Invoice: Levels.OWN,

      // Person: Levels.NO_ACCESS,
      // Person: Levels.VIEW_ONLY,
      // Person: Levels.COMMENT,
      // Person: Levels.ALTER,
      Person: Levels.OWN,

      // Product: Levels.NO_ACCESS,
      Product: Levels.VIEW_ONLY,
      // Product: Levels.COMMENT,
      // Product: Levels.ALTER,
      // Product: Levels.OWN,
    },
  },
  domains: collectDomains(),
};

const getters = {
  getUserPermissions: vx => vx.user.permissions,
};

const mutations = {
  // changeRole(vx, newRole) {
  //   LG(vx);
  //   vx.user.info.role = newRole; // eslint-disable-line no-param-reassign
  // },
  setSandBox(vx, _box) {
    vx.sandbox = _box; // eslint-disable-line no-param-reassign
  },
  changePermission(vx, chg) {
    window.lgr.debug(`Access Control (mutation) :: changePermissions of "${chg.resource}"`);
    vx.user.permissions[chg.resource] = chg.setting; // eslint-disable-line no-param-reassign
    // LG(vx.user.permissions);
  },
};

const actions = {
  resetPermissions(vx, args) {
    const { payload, ability } = args;
    const prms = JSON.parse(payload.permissions.replace(/'/g, '"'));
    Object.keys(prms).forEach((permission) => {
      const change = { resource: permission, setting: prms[permission] };
      LG(change);
      vx.dispatch('changePermissions', { change, ability });
    });


    LG(`

      Permissions reset >>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>
    `);
    LG(prms);
    LG(accessGroups);

    // window.ability.addAlias('see', ['only view']);
    // window.ability.update([{ actions: ['only view'], subject: 'doodle' }]);

    const perimeters = [];
    Object.keys(perimeterDefs).map((def) => {
      perimeters.push(perimeterDefs[def]);
      return def;
    });
    LG('perimeters');
    LG(perimeters);
    const box = createSandbox(currentUser.child(store), {
      perimeters,
      // perimeters: [
      //   perimeter,
      // ],
      // governess: new RouteGoverness({ from, t, nxt }),
      governess: new StrictGoverness(),
    });

    vx.commit('setSandBox', box);
    LG(vx);
  },
  changePermissions(vx, args) {
    const { change, ability } = args;

    // LG('args.ability');
    // LG(ability);
    // LG(change);
    // LG('accessGroups');
    // LG(accessGroups);
    vx.commit('changePermission', change);

    const newRules = [];
    ability.rules.map((rule) => { // eslint-disable-line arrow-body-style
      let existing = false;

      rule.subject.forEach((subj) => {
        // LG(`rule : subj = ${subj}`);
        if (!existing) existing = accessGroups[change.resource].includes(subj);
        // LG(`existing = ${existing}`);
      });

      if (existing) {
        // can(Levels.idxAliases[change.setting], accessGroups[change.resource]);
        newRules.push({
          actions: Levels.idxAliases[change.setting],
          subject: accessGroups[change.resource],
        });
        // LG(`Updated rule : setting = ${Levels.idxAliases[change.setting]}`);
        // LG(accessGroups[change.resource]);
      } else {
        // can(rule.actions, rule.subject);
        newRules.push(rule);
      }
      return rule;
    });

    ability.update(newRules);

    // LG(' -- ability -- ');
    // LG(ability.rules.forEach((rule) => {
    //   LG(rule);
    // }));
  },
};

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions,
};

const Perimeters = perimeterDefs;


const rsrc = new Set();
Object.values(Perimeters).forEach((x) => {
  rsrc.add(x.resource);
});
const Resources = Array.from(rsrc);


export { Levels, Perimeters, Resources };



